.TodoSearch {
    background: #F9FBFC;
    border-radius: 50px;
    border: none;
    margin: 0 24px;
    height: 64px;
    width: calc(62% - 50px);
    font-size: 24px;
    text-align: center;
    font-family: 'Montserrat';
    font-weight: 400;
    color: #1E1E1F;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
}

.TodoSearch::placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
}

.TodoSearch:focus {
    outline-color: #000000;
}

.content-todoSearch {
    text-align: center;
}